
.zpore-control-header.next-leak-controller.showing {
    animation-name: appear;
    animation-duration: 2.5s;
    opacity: 1;
    pointer-events: none;

    .load-into-spore, .close-btn {
        pointer-events: auto;
    }
}

.zpore-control-header.next-leak-controller {
    z-index: 300000000 !important;
    top: unset;
    bottom: -18px;
    left: 0px;
    right: 0px;
    margin-left: auto;
    margin-right: auto;
    white-space: nowrap;
    .inner-control {
        padding-bottom: 5px;
    }

    .close-btn {
        pointer-events: auto !important;
        cursor: pointer;
        margin-top: 0px;
    }

    .clock {
        margin-right: 5px;
    }

    .marque-container {
        max-width: 260px;
        overflow: hidden;
        font-size: 6px !important;
        padding: 0px !important;
        height: 12px;
        line-height: 5px;
        margin-bottom: 5px;
        .marque {
            padding: 0px !important;
            padding-left: 15px !important;

            //animation: marque 15s linear infinite;
            white-space: nowrap;
        }
    }

    .zorb-multiplayer {
        transform: scale(1.2);
        animation: blinky 0.5s infinite linear;
        animation-direction: alternate;
    }

    .load-into-spore {
        white-space: break-spaces !important;
        border-radius: 10px;
        font-size: 10pt !important;
        background-color: #7e14ff;
        transform: translate(0px, -5px);

        div {
         background: linear-gradient(60deg, #03fe18 0%, #85ff75e0 25%, rgb(60 220 238) 50%, #ffbdec 75%, #eeb2d8 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            display: inline-block;
            -webkit-mask:linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
            background-repeat: no-repeat;
            animation: shimmer 2.5s infinite;
        }

       //padding: 10px !important;
    }
    .inner-control {
        .top-header {
            background-color: unset;
        }
    }
}

.zpore-control-header {
    position: fixed;
    z-index: 30;
    width: 100%;
    max-width: 500px;
    top: 15px;
    left: 15px;

    svg {
        pointer-events: none;
    }

    .ens-name {
        color: white;
        margin-left: 5px;
    }

    .inner-control {
        .top-header {
            background-color: #c3c3c3;
        }
        .simple-controls {
            position: unset !important;

            .play-btn-wrapper {
                width: 40px;
                height: unset;
                margin: 0px !important;
                padding: 0px;
            }
        }
        border-radius: 13px !important;
        padding: 1px;

        .remixes-btn {
            margin-bottom: 5px;
            margin-top: -8px;
            width: 120px;
            border-bottom: 1px solid transparent;
            cursor: pointer;
            padding-bottom: 0px !important;
        }

        .remixes-btn:hover {
            border-bottom-color: white;
            animation-duration: 1s;
            animation-name: hover-remixes;
        }
        
    }
}

@keyframes hover-remixes {
    from {
        border-bottom-color: transparent;
    }
    to {
        border-bottom-color: white;
    }
}

@media screen and (max-width: 600px) {
    .zpore-control-header.next-leak-controller {
        .play-btn-wrapper {
            margin-top: 5px;
            width: 30px !important;
            height: unset;
            margin: 0px !important;
            padding: 0px;
        }

        .close-btn {
            margin-top: 0px;
        }

        .clock {
            white-space: nowrap;
            margin-right: 10px;
        }
 
    }
    .zpore-control-header {
        top: 0px;
        left: 0px;

    }
}

@keyframes marque {
    0%   { transform: translateX(100%); }
    100% { transform: translateX(-100%); }
}

@keyframes blinky {
    0% {
        transform: scale(1);
    }

    100% {
        transform: scale(1.2);
    }
}

@keyframes appear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
