.leak-controller {
    position: fixed !important;

    bottom: 0px;
    top: 0px;
    margin-top: auto;
    margin-bottom: auto;
    transform: translate(0px, 35px);
    pointer-events: none;
    left: -20px;
    padding-left: 20px;

    .zorbs {
        margin-left: -12px;
        mix-blend-mode: color-burn;
        img {
            mix-blend-mode: color-burn;
        }
        .second {
            margin-left: -6px;
        }
    }

    .close-icon {
        pointer-events: auto !important;
        cursor: pointer !important;
        position: absolute;
        top: 8px;
        left: 19px;
        z-index: 10000 !important;
    }
    
    .z-30 {
        display: flex;
        flex-direction: column;
        min-width: 200px;
    }

    .ens-name {
        margin-top: 4px;
        color: white;
        
    }
}

@media screen and (max-width: 600px) {
    .leak-controller {
        left: unset;
        right: -55px;
        padding-right: 20px;
        padding-left: 10px;
        margin-bottom: unset;
        bottom: 110px;
        transform: scale(0.7);

        .leak-label {
            transform: scale(0.7);
            transform-origin: top left;
        }

        .close-icon {
            left: 2px;
            transform-origin: top left;
            transform: scale(1.5);
        }
    }
}
